
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';

import './Portfolio.css';
import PortfolioCard from './PortfolioCard';
import projects from './projects';

// 
// For styles
//
const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: "auto",
    height: "auto",
  },
});

//
// Portfolio
//
class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      projects: this.initProjects(),
    };
    this.handleCardExpandClick = this.handleCardExpandClick.bind(this);
  }

  initProjects() {
    return projects.slice().map(prj => {
      prj.expanded = false;
      return prj;
    });
  }

  handleCardExpandClick(i) {
    const prj = this.state.projects.slice();
    
    prj[i].expanded = !this.state.projects[i].expanded;
    this.setState({ projects: prj });
  }

  renderProjects() {
    const { classes } = this.props;

    return (
      <GridList cellHeight="auto" className={classes.gridList}>
      {
        this.state.projects.map((prj, i) => (
          <div key={i.toString()}>
            <PortfolioCard
              project={prj}
              onClick={() => this.handleCardExpandClick(i)}
            />
          </div>
        ))
      }
      </GridList>
    );
  }

  render() {
    const { classes } = this.props;

    return (
      <section className="portfolio">
        {this.renderProjects()}
      </section>
    );
  }
}

export default withStyles(styles)(Portfolio);
